// –†–µ—Ñ-—Å—á—ë—Ç—á–∏–∫ + ¬´–∞–≤–∞—Ä–∏–π–Ω–∞—è¬ª —Ä–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∫–∞
let depth = 0;
let savedY = 0;

export function lockScroll() {
  if (depth++ === 0) {
    savedY = window.scrollY || document.documentElement.scrollTop || 0;
    const s = document.body.style;
    s.position = "fixed";
    s.top = `-${savedY}px`;
    s.left = "0";
    s.right = "0";
    s.width = "100%";
    document.body.classList.add("scroll-locked");
  }
}

export function unlockScroll(force = false) {
  if (force) depth = 1;                // –ø—Ä–∏–Ω—É–¥–∏—Ç–µ–ª—å–Ω–æ ¬´—Å–≤–µ—Å—Ç–∏¬ª –∫ –Ω—É–ª—é
  if (depth > 0 && --depth === 0) {
    const s = document.body.style;
    s.position = s.top = s.left = s.right = s.width = "";
    window.scrollTo(0, savedY);
    document.body.classList.remove("scroll-locked");
  }
}

// –Ω–∞ –≤—Å—è–∫–∏–π —Å–ª—É—á–∞–π ‚Äî –¥–æ—Å—Ç—É–ø–Ω–æ –∏–∑ –∫–æ–Ω—Å–æ–ª–∏: window.__unlockScroll()
export function emergencyUnlock() { unlockScroll(true); }
if (typeof window !== "undefined") {
  window.__unlockScroll = emergencyUnlock;
  // –µ—Å–ª–∏ —Å—Ç—Ä–∞–Ω–∏—Ü–∞ –ø–µ—Ä–µ–≥—Ä—É–∂–∞–µ—Ç—Å—è –≤ –∑–∞—Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏ ‚Äî —Å–Ω–∏–º–µ–º –±–ª–æ–∫–∏—Ä–æ–≤–∫—É
  window.addEventListener("pageshow", () => emergencyUnlock());
}
