// –ì–õ–û–ë–ê–õ–¨–ù–´–ï SFX –î–õ–Ø –í–°–ï–ì–û –°–ê–ô–¢–ê
// –ü–æ–¥–∫–ª—é—á–∏—Ç–µ –æ–¥–∏–Ω —Ä–∞–∑ –≤ main.js:  import "./js/global-sfx.js";
import { UISound } from "./artists/lib/sound.js"; // –ø—É—Ç—å –ø–æ–¥ –≤–∞—à –ø—Ä–æ–µ–∫—Ç

// ===== –ö–æ–Ω—Ñ–∏–≥ =====
const CLICKABLE_SELECTOR = [
  'a[href]',
  'button',
  '[role="button"]',
  'input[type="button"]',
  'input[type="submit"]',
  'input[type="reset"]',
  'summary',
  '.sfx-click'          // –æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π ¬´–º–∞—Ä–∫–µ—Ä-–∫–ª–∞—Å—Å¬ª
].join(',');

// –ì–¥–µ –ù–ï –∏–≥—Ä–∞—Ç—å (–ø–æ–ª–∑—É–Ω–∫–∏, –¥—Ä–∞–≥-–∑–æ–Ω—ã –∏ —Ç.–ø.)
const IGNORE_SELECTOR = [
  '.no-sfx',
  '[data-sfx="off"]',
  '[disabled],[aria-disabled="true"]',
  'input[type="range"]',
  '.am-player__dragzone', // –≤–∞—à–∞ –≤–µ—Ä—Ö–Ω—è—è –∑–æ–Ω–∞ –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è –ø–ª–µ–µ—Ä–∞
].join(',');

// ===== –°–µ—Ä–≤–∏—Å =====
const SFX = {
  enabled: localStorage.getItem('sfx') !== 'off',
  lastAt: 0,
  playOnce() {
    if (!this.enabled) return;
    const now = performance.now();
    if (now - this.lastAt < 80) return; // –∞–Ω—Ç–∏-–¥—É–±–ª—å
    this.lastAt = now;
    try {
      UISound?.tap?.(); // –≤–∞—à —Ç–µ–∫—É—â–∏–π –∑–≤—É–∫
    } catch {
      // –§–æ–ª–±—ç–∫ –º–∏–Ω–∏-–±–∏–ø–æ–º (–µ—Å–ª–∏ –≤–¥—Ä—É–≥ UISound –Ω–µ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω)
      try {
        const ctx = new (window.AudioContext || window.webkitAudioContext)();
        const o = ctx.createOscillator();
        const g = ctx.createGain();
        o.type = 'triangle'; o.frequency.value = 660;
        g.gain.setValueAtTime(0.06, ctx.currentTime);
        g.gain.exponentialRampToValueAtTime(0.0001, ctx.currentTime + 0.06);
        o.connect(g).connect(ctx.destination); o.start(); o.stop(ctx.currentTime + 0.07);
      } catch {}
    }
  },
};

// –ì–ª–æ–±–∞–ª—å–Ω—ã–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–æ–≤
document.addEventListener('click', (e) => {
  // —Ç–æ–ª—å–∫–æ ¬´–Ω–∞—Å—Ç–æ—è—â–∏–µ¬ª –ª–µ–≤—ã–µ –∫–ª–∏–∫–∏
  if (!e.isTrusted || e.button !== 0 || e.metaKey || e.ctrlKey || e.altKey) return;

  const el = e.target.closest(CLICKABLE_SELECTOR);
  if (!el) return;
  if (el.matches(IGNORE_SELECTOR)) return;

  // –ï—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ç–∞—â–∏–ª–∏ (drag), –±—Ä–∞—É–∑–µ—Ä –æ–±—ã—á–Ω–æ –Ω–µ –∫–∏–¥–∞–µ—Ç click ‚Äî –Ω–æ –Ω–∞ –≤—Å—è–∫–∏–π —Å–ª—É—á–∞–π:
  if (Math.abs((e.movementX || 0)) > 2 || Math.abs((e.movementY || 0)) > 2) return;

  SFX.playOnce();
}, { capture: true });

// –ö–ª–∞–≤–∏–∞—Ç—É—Ä–Ω–∞—è –∞–∫—Ç–∏–≤–∞—Ü–∏—è (Enter/Space) ‚Äî –¥–ª—è a11y
document.addEventListener('keydown', (e) => {
  if (!SFX.enabled) return;
  if (!(e.key === 'Enter' || e.key === ' ')) return;

  const el = e.target.closest(CLICKABLE_SELECTOR);
  if (!el || el.matches(IGNORE_SELECTOR)) return;

  SFX.playOnce();
}, { capture: true });

// –ü—É–±–ª–∏—á–Ω—ã–π —Ç—É–º–±–ª–µ—Ä (–º–æ–∂–Ω–æ –ø—Ä–∏–≤—è–∑–∞—Ç—å –∫ –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º)
window.SFX = {
  enable()  { SFX.enabled = true;  localStorage.setItem('sfx', 'on');  },
  disable() { SFX.enabled = false; localStorage.setItem('sfx', 'off'); },
  toggle()  { SFX.enabled ? this.disable() : this.enable(); },
};
