// –ù–∏—á–µ–≥–æ –Ω–µ –ª–æ–º–∞–µ—Ç —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–µ —á–∞—Å—Ç–∏: initArtists + –º–∏–Ω–∏-–ø–ª–µ–µ—Ä.

