import { initGrid } from "./grid.js";
import { createArtistModal } from "./modal.js";
import { initRouter } from "./router.js";
import { initPrefetch } from "./prefetch.js";

export function initArtists(root = document.querySelector("#artists-section")) {
  if (!root) return;

  // URL-sync —Å–Ω–∞—á–∞–ª–∞ (–≤–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç —Å–æ—Å—Ç–æ—è–Ω–∏–µ –¥–æ –Ω–∞—á–∞–ª—å–Ω–æ–π –∑–∞–≥—Ä—É–∑–∫–∏)
  initRouter(root);

  // –ú–æ–¥–∞–ª–∫–∞ (–≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç open/close)
  const modal = createArtistModal(root);

  // Prefetch "Learn more" (—É—Å–∫–æ—Ä—è–µ—Ç –æ—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª–∫–∏)
  initPrefetch(root);

  // –ì—Ä–∏–¥ (–ø–µ—Ä–µ–¥–∞—ë–º –º–æ–¥–∞–ª–∫—É –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è)
  initGrid(root, { openModal: modal.open });
}
